<template>
  <div class="main_navs">
    <div class="modal_main" :class="{'active':isActive}">
      <button @click="exit_click()" class="exit_modal">
        <svg width="15" height="15" viewBox="0 0 512 512" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" clip-rule="evenodd" d="M84.9407 448.942C78.6923 455.19 68.5616 455.19 62.3132 448.942C56.0649 442.693 56.0649 432.563 62.3132 426.314L233 255.628L62.3132 84.9417C56.0649 78.6933 56.0649 68.5626 62.3132 62.3142C68.5616 56.0658 78.6923 56.0658 84.9407 62.3142L255.627 233.001L426.313 62.3142C432.562 56.0658 442.692 56.0658 448.941 62.3142C455.189 68.5626 455.189 78.6933 448.941 84.9417L278.254 255.628L448.941 426.314C455.189 432.563 455.189 442.693 448.941 448.942C442.692 455.19 432.562 455.19 426.313 448.942L255.627 278.255L84.9407 448.942Z" fill="black"/>
        </svg>
      </button>
      <div class="create_code">
        <form @submit.prevent="createCode()">
          <div>
            <div class="title_code">Tasdiqlash kodi!</div>
            <div class="code_input">
              <input type="text" v-model="code">
            </div>
            <div class="code_btn">
              <button>Tasdiqlash</button>
            </div>
          </div>
        </form>
      </div>
    </div>
    <Header></Header>

    <div class="container">
      <div class="login">
        <div class="row">
          <div class="col-xl-5">
            <div class="log_img">
              <div class="log_img_title">{{$t('login')}}</div>
              <img src="../../../assets/images/llaptop.png" alt="">
            </div>
          </div>
          <div class="col-xl-7">
            <div class="log_form">
              <div>
                <form @submit.prevent="regIstration()">
                  <label>{{$t('phone')}} *</label>

                  <phone-mask-input
                      v-model="phone"
                      autoDetectCountry
                      showFlag
                      wrapperClass="wrraper-example"
                      inputClass="input-example"
                      flagClass="flag-example"
                  />
                  <label>{{$t('password')}}*</label>
                  <div class="input_text">
                    <input  :type="passwordFieldType"
                           id="password"
                           v-model="password" :placeholder="$t('password')">
                    <a @click="togglePasswordVisibility" class="password_show" href="#">
                      <b-icon icon="eye" aria-hidden="true"></b-icon>
                    </a>
                  </div>
                  <div class="forgot_password">
                    <router-link to="/" class="decoration">
                     {{$t('forget_password')}}
                    </router-link>
                  </div>
                  <div class="log_btn">
                    <button>{{$t('login')}}</button>
                  </div>
                  <div class="phone_link">
                    <router-link to="/email-login" >
                      <div>
                        Email orqali kirish
                      </div>
                    </router-link>
                  </div>
                </form>
                <div class="reg_links">
                  <router-link to="/phone_reg">
                    <div>{{$t('reg_quiz')}}? <span>{{$t('reg')}}</span></div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
import PhoneMaskInput from  "vue-phone-mask-input";
export default {
  components: {Footer, Header,PhoneMaskInput},
  data(){
    return{
      email:'',
      password:'',
      phone:'',
      code:'',
      isActive:false,
      passwordVisible: false
    }
  },
  computed: {
    passwordFieldType() {
      return this.passwordVisible ? 'text' : 'password';
    }
  },
  methods:{
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    },
    regIstration(){
      this.$axiosDefault.post('/students/login/',{
        "phone_number":this.phone,
        "password":this.password
      })
          .then(res=>{
            localStorage.setItem('reg_token',res.data.access)
            this.$store.dispatch('getMe')
            this.$router.push('/')
          })
          .catch(()=>{
            this.$toast("Login yoki parol xato", {
              timeout: 1000,
              type:"error"
            });
            this.code=''
          })

    },

  }

}
</script>
